import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Typography,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Button,
  TextField,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Chip,
  Alert,
  Snackbar,
  Grid,
  IconButton,
} from '@mui/material';
import {
  Add,
  Edit,
  Delete,
  Search,
  Refresh,
} from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';

interface Package {
  id: string;
  trackingNumber: string;
  status: string;
  recipient: string;
  phone: string;
  address: string;
  createdAt: string;
}

const AdminPackages: React.FC = () => {
  const navigate = useNavigate();
  const [packages, setPackages] = useState<Package[]>([]);
  const [loading, setLoading] = useState(true);
  const [openDialog, setOpenDialog] = useState(false);
  const [editingPackage, setEditingPackage] = useState<Package | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [alert, setAlert] = useState<{ open: boolean; message: string; severity: 'success' | 'error' }>({
    open: false,
    message: '',
    severity: 'success',
  });

  // 模拟数据加载
  useEffect(() => {
    const loadPackages = async () => {
      setLoading(true);
      try {
        // 模拟API调用
        const mockData: Package[] = [
          {
            id: '1',
            trackingNumber: 'ML2024001',
            status: '已入库',
            recipient: '张三',
            phone: '09-12345678',
            address: '仰光市中心',
            createdAt: '2024-01-01',
          },
          {
            id: '2',
            trackingNumber: 'ML2024002',
            status: '运输中',
            recipient: '李四',
            phone: '09-87654321',
            address: '曼德勒市',
            createdAt: '2024-01-02',
          },
        ];
        setPackages(mockData);
      } catch (error) {
        console.error('加载数据失败:', error);
      } finally {
        setLoading(false);
      }
    };

    loadPackages();
  }, []);

  const getStatusColor = (status: string) => {
    switch (status) {
      case '已入库': return 'primary';
      case '运输中': return 'warning';
      case '待签收': return 'info';
      case '已签收': return 'success';
      default: return 'default';
    }
  };

  const handleAdd = () => {
    setEditingPackage(null);
    setOpenDialog(true);
  };

  const handleEdit = (pkg: Package) => {
    setEditingPackage(pkg);
    setOpenDialog(true);
  };

  const handleDelete = (id: string) => {
    setPackages(packages.filter(p => p.id !== id));
    setAlert({ open: true, message: '包裹删除成功', severity: 'success' });
  };

  const handleSave = () => {
    if (editingPackage) {
      const existingIndex = packages.findIndex(p => p.id === editingPackage.id);
      if (existingIndex >= 0) {
        const updatedPackages = [...packages];
        updatedPackages[existingIndex] = editingPackage;
        setPackages(updatedPackages);
      } else {
        setPackages([...packages, { ...editingPackage, id: Date.now().toString() }]);
      }
      setAlert({ open: true, message: '保存成功', severity: 'success' });
    }
    setOpenDialog(false);
  };

  const filteredPackages = packages.filter(pkg =>
    pkg.trackingNumber.toLowerCase().includes(searchTerm.toLowerCase()) ||
    pkg.recipient.toLowerCase().includes(searchTerm.toLowerCase()) ||
    pkg.phone.includes(searchTerm)
  );

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4" component="h1">
          跨境包裹管理
        </Typography>
        <Button variant="contained" startIcon={<Add />} onClick={handleAdd}>
          新增包裹
        </Button>
      </Box>

      <Paper sx={{ mb: 3 }}>
        <Box sx={{ p: 2, display: 'flex', gap: 2 }}>
          <TextField
            fullWidth
            variant="outlined"
            placeholder="搜索包裹号、收件人或手机号..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            InputProps={{
              startAdornment: <Search sx={{ color: 'action.active', mr: 1 }} />,
            }}
          />
          <Button variant="outlined" startIcon={<Refresh />} onClick={() => window.location.reload()}>
            刷新
          </Button>
        </Box>
      </Paper>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>包裹号</TableCell>
              <TableCell>状态</TableCell>
              <TableCell>收件人</TableCell>
              <TableCell>手机号</TableCell>
              <TableCell>地址</TableCell>
              <TableCell>创建时间</TableCell>
              <TableCell>操作</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {loading ? (
              <TableRow>
                <TableCell colSpan={7} align="center">
                  加载中...
                </TableCell>
              </TableRow>
            ) : filteredPackages.length === 0 ? (
              <TableRow>
                <TableCell colSpan={7} align="center">
                  暂无数据
                </TableCell>
              </TableRow>
            ) : (
              filteredPackages.map((pkg) => (
                <TableRow key={pkg.id} hover>
                  <TableCell>{pkg.trackingNumber}</TableCell>
                  <TableCell>
                    <Chip label={pkg.status} color={getStatusColor(pkg.status) as any} size="small" />
                  </TableCell>
                  <TableCell>{pkg.recipient}</TableCell>
                  <TableCell>{pkg.phone}</TableCell>
                  <TableCell>{pkg.address}</TableCell>
                  <TableCell>{pkg.createdAt}</TableCell>
                  <TableCell>
                    <IconButton size="small" onClick={() => handleEdit(pkg)}>
                      <Edit />
                    </IconButton>
                    <IconButton size="small" onClick={() => handleDelete(pkg.id)}>
                      <Delete />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))
            )}
          </TableBody>
        </Table>
      </TableContainer>

      {/* 添加/编辑对话框 */}
      <Dialog open={openDialog} onClose={() => setOpenDialog(false)} maxWidth="md" fullWidth>
        <DialogTitle>
          {editingPackage ? '编辑包裹' : '新增包裹'}
        </DialogTitle>
        <DialogContent>
          <Grid container spacing={2} sx={{ mt: 1 }}>
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label="包裹号"
                value={editingPackage?.trackingNumber || ''}
                onChange={(e) => setEditingPackage(prev => prev ? { ...prev, trackingNumber: e.target.value } : null)}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label="状态"
                value={editingPackage?.status || ''}
                onChange={(e) => setEditingPackage(prev => prev ? { ...prev, status: e.target.value } : null)}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label="收件人"
                value={editingPackage?.recipient || ''}
                onChange={(e) => setEditingPackage(prev => prev ? { ...prev, recipient: e.target.value } : null)}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label="手机号"
                value={editingPackage?.phone || ''}
                onChange={(e) => setEditingPackage(prev => prev ? { ...prev, phone: e.target.value } : null)}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                label="地址"
                value={editingPackage?.address || ''}
                onChange={(e) => setEditingPackage(prev => prev ? { ...prev, address: e.target.value } : null)}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenDialog(false)}>取消</Button>
          <Button onClick={handleSave} variant="contained">保存</Button>
        </DialogActions>
      </Dialog>

      <Snackbar
        open={alert.open}
        autoHideDuration={3000}
        onClose={() => setAlert({ ...alert, open: false })}
      >
        <Alert severity={alert.severity} onClose={() => setAlert({ ...alert, open: false })}>
          {alert.message}
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default AdminPackages;
