import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Box,
  Grid,
  Card,
  CardContent,
  CardActions,
  Button,
  Chip,
} from '@mui/material';
import {
  LocalShipping,
  TrackChanges,
  People,
  Assessment,
} from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';

interface Package {
  id: string;
  trackingNumber: string;
  status: string;
  recipient: string;
  phone: string;
  address: string;
  createdAt: string;
}

const AdminDashboard: React.FC = () => {
  const navigate = useNavigate();
  const [packages, setPackages] = useState<Package[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadPackages = async () => {
      setLoading(true);
      try {
        // 模拟API调用 - 加载localStorage中的数据
        const storedPackages = localStorage.getItem('packages');
        if (storedPackages) {
          const parsedPackages = JSON.parse(storedPackages);
          setPackages(parsedPackages.slice(0, 5)); // 只显示前5个
        }
      } catch (error) {
        console.error('加载数据失败:', error);
      } finally {
        setLoading(false);
      }
    };

    loadPackages();
  }, []);

  const getStatusColor = (status: string) => {
    switch (status) {
      case '已入库': return 'primary';
      case '运输中': return 'warning';
      case '待签收': return 'info';
      case '已签收': return 'success';
      default: return 'default';
    }
  };

  const getTotalByStatus = (status: string) => {
    return packages.filter(pkg => pkg.status === status).length;
  };

  const stats = [
    { label: '已入库', count: getTotalByStatus('已入库'), icon: <LocalShipping />, color: 'primary' },
    { label: '运输中', count: getTotalByStatus('运输中'), icon: <TrackChanges />, color: 'warning' },
    { label: '待签收', count: getTotalByStatus('待签收'), icon: <People />, color: 'info' },
    { label: '已签收', count: getTotalByStatus('已签收'), icon: <Assessment />, color: 'success' },
  ];

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4" component="h1">
          管理后台
        </Typography>
        <Button variant="contained" onClick={() => navigate('/admin/inventory')}>
          查看所有包裹
        </Button>
      </Box>

      {/* 统计卡片 */}
      <Grid container spacing={3} sx={{ mb: 4 }}>
        {stats.map((stat, index) => (
          <Grid item xs={12} sm={6} md={3} key={index}>
            <Card>
              <CardContent>
                <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                  <Box>
                    <Typography variant="h4" component="div">
                      {stat.count}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {stat.label}
                    </Typography>
                  </Box>
                  <Box sx={{ color: `${stat.color}.main` }}>
                    {stat.icon}
                  </Box>
                </Box>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>

      {/* 最近包裹 */}
      <Typography variant="h5" component="h2" gutterBottom>
        最近包裹
      </Typography>

      <Grid container spacing={2}>
        {loading ? (
          <Grid item xs={12}>
            <Typography>加载中...</Typography>
          </Grid>
        ) : packages.length === 0 ? (
          <Grid item xs={12}>
            <Typography color="text.secondary">暂无包裹数据</Typography>
          </Grid>
        ) : (
          packages.map((pkg) => (
            <Grid item xs={12} sm={6} md={4} key={pkg.id}>
              <Card>
                <CardContent>
                  <Typography variant="h6" gutterBottom>
                    {pkg.trackingNumber}
                  </Typography>
                  <Chip
                    label={pkg.status}
                    color={getStatusColor(pkg.status) as any}
                    size="small"
                    sx={{ mb: 2 }}
                  />
                  <Typography variant="body2" color="text.secondary">
                    收件人: {pkg.recipient}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    手机号: {pkg.phone}
                  </Typography>
                </CardContent>
                <CardActions>
                  <Button size="small" onClick={() => navigate('/admin/inventory')}>
                    查看详情
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))
        )}
      </Grid>

      {/* 快速操作 */}
      <Typography variant="h5" component="h2" gutterBottom sx={{ mt: 4 }}>
        快速操作
      </Typography>

      <Grid container spacing={2}>
        <Grid item xs={12} sm={6} md={3}>
          <Button
            fullWidth
            variant="outlined"
            onClick={() => navigate('/admin/inventory')}
            sx={{ py: 3 }}
          >
            包裹管理
          </Button>
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <Button
            fullWidth
            variant="outlined"
            onClick={() => navigate('/admin/data-merger')}
            sx={{ py: 3 }}
          >
            数据合并
          </Button>
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <Button
            fullWidth
            variant="outlined"
            sx={{ py: 3 }}
          >
            财务管理
          </Button>
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <Button
            fullWidth
            variant="outlined"
            sx={{ py: 3 }}
          >
            运输管理
          </Button>
        </Grid>
      </Grid>
    </Container>
  );
};

export default AdminDashboard;
