import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Box,
  Paper,
  Button,
  Alert,
  Snackbar,
  Grid,
  Card,
  CardContent,
  LinearProgress,
  List,
  ListItem,
  ListItemText,
  Divider,
} from '@mui/material';

interface MergeStatus {
  local: number;
  cloud: number;
  onlyLocal: number;
  onlyCloud: number;
  conflicts: number;
}

const DataMerger: React.FC = () => {
  const [localData, setLocalData] = useState<any>({ packages: [], finance: [], users: [] });
  const [cloudData, setCloudData] = useState<any>({ packages: [], finance: [], users: [] });
  const [mergeStatus, setMergeStatus] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [alert, setAlert] = useState<{ open: boolean; message: string; severity: 'success' | 'error' }>({
    open: false,
    message: '',
    severity: 'success',
  });

  useEffect(() => {
    loadAllData();
  }, []);

  const loadAllData = async () => {
    setLoading(true);
    try {
      // 加载localStorage数据
      const localPackages = JSON.parse(localStorage.getItem('packages') || '[]');
      const localFinance = JSON.parse(localStorage.getItem('finance') || '[]');
      const localUsers = JSON.parse(localStorage.getItem('users') || '[]');

      setLocalData({ packages: localPackages, finance: localFinance, users: localUsers });

      // 模拟云端数据（实际项目中应该从Supabase加载）
      const mockCloudData = {
        packages: [],
        finance: [],
        users: [],
      };

      setCloudData(mockCloudData);
    } catch (error) {
      console.error('加载数据失败:', error);
      setAlert({ open: true, message: '加载数据失败', severity: 'error' });
    } finally {
      setLoading(false);
    }
  };

  const analyzeData = () => {
    if (!localData || !cloudData) return;

    const status: any = {
      packages: { local: 0, cloud: 0, onlyLocal: 0, onlyCloud: 0, conflicts: 0 },
      finance: { local: 0, cloud: 0, onlyLocal: 0, onlyCloud: 0 },
      users: { local: 0, cloud: 0, onlyLocal: 0, onlyCloud: 0 },
    };

    // 分析包裹数据
    status.packages.local = localData.packages.length;
    status.packages.cloud = cloudData.packages.length;
    status.packages.onlyLocal = localData.packages.length;
    status.packages.onlyCloud = cloudData.packages.length;
    status.packages.conflicts = 0;

    // 分析财务数据
    status.finance.local = localData.finance.length;
    status.finance.cloud = cloudData.finance.length;
    status.finance.onlyLocal = localData.finance.length;
    status.finance.onlyCloud = cloudData.finance.length;

    // 分析用户数据
    status.users.local = localData.users.length;
    status.users.cloud = cloudData.users.length;
    status.users.onlyLocal = localData.users.length;
    status.users.onlyCloud = cloudData.users.length;

    setMergeStatus(status);
  };

  const mergeData = async () => {
    setLoading(true);
    try {
      // 模拟合并过程
      const mergedPackages = [...localData.packages];
      const mergedFinance = [...localData.finance];
      const mergedUsers = [...localData.users];

      // 保存到localStorage（实际项目中应该保存到Supabase）
      localStorage.setItem('packages', JSON.stringify(mergedPackages));
      localStorage.setItem('finance', JSON.stringify(mergedFinance));
      localStorage.setItem('users', JSON.stringify(mergedUsers));

      setAlert({ open: true, message: '数据合并成功！', severity: 'success' });
      analyzeData(); // 重新分析数据
    } catch (error) {
      console.error('合并数据失败:', error);
      setAlert({ open: true, message: '数据合并失败', severity: 'error' });
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Typography variant="h4" component="h1" gutterBottom>
        数据合并工具
      </Typography>
      <Typography variant="body1" color="text.secondary" sx={{ mb: 4 }}>
        将本地数据安全地合并到云端数据库中，防止数据丢失。
      </Typography>

      <Grid container spacing={3}>
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                本地数据 (localStorage)
              </Typography>
              <Typography variant="body2" color="text.secondary">
                • 包裹: {localData.packages.length} 条<br />
                • 财务: {localData.finance.length} 条<br />
                • 用户: {localData.users.length} 条
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                云端数据 (Supabase)
              </Typography>
              <Typography variant="body2" color="text.secondary">
                • 包裹: {cloudData.packages.length} 条<br />
                • 财务: {cloudData.finance.length} 条<br />
                • 用户: {cloudData.users.length} 条
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                操作
              </Typography>
              <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
                <Button variant="outlined" onClick={analyzeData}>
                  分析数据
                </Button>
                <Button variant="contained" onClick={mergeData} disabled={loading}>
                  {loading ? '合并中...' : '合并到云端'}
                </Button>
                <Button variant="outlined" onClick={loadAllData}>
                  刷新数据
                </Button>
              </Box>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {loading && (
        <Box sx={{ mt: 3 }}>
          <LinearProgress />
        </Box>
      )}

      {mergeStatus && (
        <Paper sx={{ p: 3, mt: 3 }}>
          <Typography variant="h6" gutterBottom>
            数据分析结果
          </Typography>

          <Grid container spacing={3}>
            <Grid item xs={12} md={4}>
              <Typography variant="subtitle1" gutterBottom>
                包裹数据
              </Typography>
              <List dense>
                <ListItem>
                  <ListItemText primary={`本地数据: ${mergeStatus.packages.local} 条`} />
                </ListItem>
                <ListItem>
                  <ListItemText primary={`云端数据: ${mergeStatus.packages.cloud} 条`} />
                </ListItem>
                <ListItem>
                  <ListItemText primary={`仅本地: ${mergeStatus.packages.onlyLocal} 条`} />
                </ListItem>
                <ListItem>
                  <ListItemText primary={`冲突: ${mergeStatus.packages.conflicts} 条`} />
                </ListItem>
              </List>
            </Grid>

            <Grid item xs={12} md={4}>
              <Typography variant="subtitle1" gutterBottom>
                财务数据
              </Typography>
              <List dense>
                <ListItem>
                  <ListItemText primary={`本地数据: ${mergeStatus.finance.local} 条`} />
                </ListItem>
                <ListItem>
                  <ListItemText primary={`云端数据: ${mergeStatus.finance.cloud} 条`} />
                </ListItem>
                <ListItem>
                  <ListItemText primary={`仅本地: ${mergeStatus.finance.onlyLocal} 条`} />
                </ListItem>
              </List>
            </Grid>

            <Grid item xs={12} md={4}>
              <Typography variant="subtitle1" gutterBottom>
                用户数据
              </Typography>
              <List dense>
                <ListItem>
                  <ListItemText primary={`本地数据: ${mergeStatus.users.local} 条`} />
                </ListItem>
                <ListItem>
                  <ListItemText primary={`云端数据: ${mergeStatus.users.cloud} 条`} />
                </ListItem>
                <ListItem>
                  <ListItemText primary={`仅本地: ${mergeStatus.users.onlyLocal} 条`} />
                </ListItem>
              </List>
            </Grid>
          </Grid>
        </Paper>
      )}

      <Alert severity="info" sx={{ mt: 3 }}>
        <Typography variant="body2">
          <strong>安全提示：</strong>合并前请确保备份重要数据。合并操作会将本地数据保存到云端，但不会删除任何现有数据。
        </Typography>
      </Alert>

      <Snackbar
        open={alert.open}
        autoHideDuration={4000}
        onClose={() => setAlert({ ...alert, open: false })}
      >
        <Alert severity={alert.severity} onClose={() => setAlert({ ...alert, open: false })}>
          {alert.message}
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default DataMerger;
