import React from 'react';
import {
  Container,
  Typography,
  Box,
  Button,
  Grid,
  Card,
  CardContent,
  CardActions,
} from '@mui/material';
import { Link } from 'react-router-dom';
import { LocalShipping, TrackChanges, Support, Speed } from '@mui/icons-material';

const HomePage: React.FC = () => {
  const features = [
    {
      icon: <LocalShipping fontSize="large" />,
      title: '跨境快递',
      description: '专业的跨境快递服务，覆盖缅甸全境',
    },
    {
      icon: <TrackChanges fontSize="large" />,
      title: '包裹追踪',
      description: '实时追踪您的包裹状态，信息透明',
    },
    {
      icon: <Support fontSize="large" />,
      title: '专业服务',
      description: '专业的客户服务团队，随时为您服务',
    },
    {
      icon: <Speed fontSize="large" />,
      title: '快速送达',
      description: '高效的物流网络，确保快速送达',
    },
  ];

  return (
    <Box>
      {/* Hero Section */}
      <Box sx={{
        bgcolor: 'primary.main',
        color: 'white',
        py: 8,
        textAlign: 'center',
      }}>
        <Container maxWidth="lg">
          <Typography variant="h2" component="h1" gutterBottom>
            MARKETLINK EXPRESS
          </Typography>
          <Typography variant="h5" component="p" gutterBottom>
            缅甸领先的快递服务
          </Typography>
          <Typography variant="body1" sx={{ mb: 4, maxWidth: 600, mx: 'auto' }}>
            提供国内快递、国际快递、同城快递等服务。快速、安全、可靠的快递解决方案，让您的包裹安全送达。
          </Typography>
          <Box sx={{ display: 'flex', gap: 2, justifyContent: 'center' }}>
            <Button variant="contained" size="large" component={Link} to="/admin/login">
              管理后台
            </Button>
            <Button variant="outlined" size="large" sx={{ color: 'white', borderColor: 'white' }}>
              价格咨询
            </Button>
          </Box>
        </Container>
      </Box>

      {/* Features Section */}
      <Container maxWidth="lg" sx={{ py: 8 }}>
        <Typography variant="h3" component="h2" textAlign="center" gutterBottom>
          我们的服务
        </Typography>
        <Grid container spacing={4} sx={{ mt: 2 }}>
          {features.map((feature, index) => (
            <Grid item xs={12} sm={6} md={3} key={index}>
              <Card sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
                <CardContent sx={{ flexGrow: 1, textAlign: 'center' }}>
                  <Box sx={{ color: 'primary.main', mb: 2 }}>
                    {feature.icon}
                  </Box>
                  <Typography variant="h5" component="h3" gutterBottom>
                    {feature.title}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    {feature.description}
                  </Typography>
                </CardContent>
                <CardActions>
                  <Button size="small" color="primary">
                    了解更多
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Container>

      {/* CTA Section */}
      <Box sx={{ bgcolor: 'grey.100', py: 8 }}>
        <Container maxWidth="lg" sx={{ textAlign: 'center' }}>
          <Typography variant="h4" component="h2" gutterBottom>
            准备好开始了吗？
          </Typography>
          <Typography variant="body1" sx={{ mb: 4 }}>
            立即联系我们，获取专业快递服务
          </Typography>
          <Button variant="contained" size="large" component={Link} to="/admin/login">
            进入管理后台
          </Button>
        </Container>
      </Box>
    </Box>
  );
};

export default HomePage;
